.links {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: end;
    color:#DEE5E5 ;
    position:static;
    
}

.navbar a {
    color:white ;
    text-decoration: none;
    margin: 20px;
    font-size: 25px;
}

.links :hover, .links :active {
    color:#CF5C36;
    font-size: 170%;
    transition: 1s;    
}

.toggleButton{
    width: 100%;
    height: 100px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.toggleButton svg{
    font-size: 50px;
}

.toggleButton button{
    margin-right: 20px;
    background-color: transparent;
    border: none;
    color: #DEE5E5;
    cursor: pointer;
}

.name{
  font-size: 30px;
  color:#CF5C36 ;
  display: flex;
}

.link{
  font-size: 30px;
}

#open {
    height: 100vh;
  }
  
  #open .links {
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 100px;
  }
  
  #open a {
    width: 100%;
    text-align: center;
  }
  
  @media only screen and (max-width: 900px) {
    .navbar a {
      width: 70px;
    }
  }
  
  @media only screen and (max-width: 600px) {
    .toggleButton {
      display: flex;
    }
    #close a {
      display: none;
    }
  }
  
  @media only screen and (min-width: 600px) {
    .toggleButton {
      display: none;
    }
  
    .hiddenLinks {
      display: none;
    }
  
    #open {
      height: 100px;
    }
  
    #open .links {
      flex-direction: row;
    }
  }